import { Container } from 'components/common';
import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import { Fade } from 'react-reveal';
import fit from './Images/fitness.jpg';
import blog from './Images/blog.png';
import what from './Images/what.png';
import spot from './Images/spot.png';
import ecom from './Images/ecomm.jpg';
import yt from './Images/yt.png';
import react from './Images/react.png';
import { Wrapper, Grid } from './styles';

const useStyles = makeStyles({
  root: {
    maxWidth: 345,
  },
  media: {
    height: 140,
  },
});
export const Projects = () => {
  const classes = useStyles();
  const bigProjects = {
    projects: [
      {
        image: react,
        projectName: 'Landing Page(React and Styled Components)',
        projectDesc: 'A finserv Landing page made in React and Styled Components',
        url: 'https://github.com/themr97/ReactLandingPage',
        demourl: 'https://finservreact.netlify.app/',
      },
      {
        image: ecom,
        projectName: 'Ecommerce Application',
        projectDesc: 'MERN Stack Ecommerce application with Braintree',
        url: 'https://github.com/themr97/Ecommerce_Frontend',
        demourl: 'https://ecomm97.netlify.app/',
      },
      {
        image: blog,
        projectName: 'PhotoBloging',
        projectDesc: 'Photobloging web application with Google OAUTH! Made on MERN Stack',
        url: 'https://github.com/themr97/PhotoBlog-Backend',
        demourl: 'https://photoblog1.netlify.app/',
      },
      {
        image: fit,
        projectName: 'Fitness Logger',
        projectDesc: 'A web application to track your daily fitness routine! Made on MERN Stack',
        url: 'https://github.com/themr97/FitnessLogger-BackEnd',
        demourl: 'https://fitness-logger.netlify.app/',
      },
      {
        image: what,
        projectName: 'Team Chat',
        projectDesc: 'A Whatsapp clone made with MERN Stack Application using Pusher(Socket.io Alternative)',
        url: 'https://github.com/themr97/RoomChat-WhatsappClone-FrontEnd',
        demourl: 'admiring-lamarr-283bb4.netlify.app',
      },
      {
        image: spot,
        projectName: 'Music Player ( Spotify Clone )',
        projectDesc: 'A nice music player with lyrics viewing feature build on React, NodeJS and Spotify API',
        url: 'https://github.com/themr97/SpotifyClone-Backend',
        demourl: 'https://musicplayer69.netlify.app/',
      },
      {
        image: yt,
        projectName: 'Simple Youtube Clone',
        projectDesc: 'Youtube Clone using Only Javascript! This project was made by me in Hackathon within 12hr',
        url: 'https://github.com/themr97/Youtube-Clone',
        demourl: 'https://youthful-poincare-625f28.netlify.app/',
      },
    ],
  };

  return (
    <Wrapper as={Container} id="projects">
      <h1>Projects</h1>
      <Grid>
        {bigProjects.projects.map((project, i) => (
          <Fade duration={3000}>
            <Card className={classes.root} key={i}>
              <CardActionArea>
                <CardMedia style={{ height: 0, paddingTop: '56%' }} className={classes.media} image={project.image} />
                <CardContent>
                  <Typography gutterBottom variant="h5" component="h2">
                    {project.projectName}
                  </Typography>
                  <Typography variant="body2" color="textSecondary" component="p">
                    {project.projectDesc}
                  </Typography>
                </CardContent>
              </CardActionArea>
              <CardActions>
                <Button size="small" color="primary" href={project.url} target="_blank">
                  Github
                </Button>
                <Button size="small" color="primary" href={project.demourl} target="_blank">
                  Live Demo
                </Button>
              </CardActions>
            </Card>
          </Fade>
        ))}
      </Grid>
    </Wrapper>
  );
};
