import React, { useContext } from 'react';
import AnchorLink from 'react-anchor-link-smooth-scroll';
import { ThemeContext } from 'providers/ThemeProvider';
import { Header } from 'components/theme';
import { Container, Button } from 'components/common';
import dev from 'assets/illustrations/dev.svg';
import Fade from 'react-reveal/Fade';
import Zoom from 'react-reveal/Zoom';
import { Wrapper, IntroWrapper, Details, Thumbnail } from './styles';

export const Intro = () => {
  const { theme } = useContext(ThemeContext);

  const url = 'https://drive.google.com/file/d/16PIN9yyCHIO7CFydQMAjUQEzY7u-Q9qW/view';

  return (
    <Wrapper>
      <Header />
      <IntroWrapper as={Container}>
        <Details theme={theme}>
          <Fade left>
            <h1>Hi There!</h1>
          </Fade>
          <Zoom left duration={3000}>
            <h4>Im Mahesh and Im a Full Stack Developer!</h4>
          </Zoom>
          <Button as={AnchorLink} href="#contact">
            Contact Me
          </Button>

          <a href={url} target="_blank">
            <Button>Resume</Button>
          </a>
        </Details>
        <Thumbnail>
          <img src={dev} alt="Im Mahesh and Im a Full Stack Developer!" />
        </Thumbnail>
      </IntroWrapper>
    </Wrapper>
  );
};
